import * as api from "../lib/web3js/index";
import { show, hide } from "react-functional-modal";
import ChainInteraction from "../components/ChainInteraction/ChainInteraction";
import {MODAL_OPTIONS} from '../utils/Constants'

class ApiProxy {
  status = 'waiting'
  async request(method,params=[],options = {}){
    const apis = await import('../lib/web3js/index')
    let res = null;
    if(options.write) {
      const {subject} = options
      params.push({
        onAccept : () => {
          this.onProcessing(subject,'success')
          window.setTimeout(() => this.close(),2000)
        },
        onReject : () => {
          this.onProcessing(subject,'reject')
          window.setTimeout(() => this.close(),2000)
        }
      })
      this.onProcessing(subject,'pending')
    }
    try {
      res = await apis[method].call(this,...params)
    } catch(e){
      console.log(e)
    }
    // if(options.write){
    //   window.setTimeout(() => this.close(),3000)
    // }
    return this.processResponse(res,options)
  }

  syncRequest(method,params = [], options ={}) {
    const res = api[method].call(this,...params)  
    return this.processResponse(res,options)
  }

  close (){
    hide('transaction-box')
  }

  onProcessing(subject,status){
    this.close();
    const options = {
      ...MODAL_OPTIONS,
        style : {
          background: "rgba(0, 0, 0, 0.4)" ,
          zIndex : 2,
        },
        key : 'transaction-box'
    }
    show(<ChainInteraction title={subject} status={status} close={this.close}/>,options)
  }

  

  processResponse(res,options){
    if(options.includeResponse){
      if(res && res.response){
        return res
      } 
    } else {
      if(res && res.response){
        return res.response.data
      } 
    }
    return res
  }
}

export default new ApiProxy();